/////////////
// Imports //
/////////////
@import "normalize.css";
@import "variables";
@import "fonts";

//////////
// Body //
//////////
*, *:before, *:after {
  box-sizing: border-box;
}

body {
  font: 100%/1.5 $font-ibm-plex-mono, monospace;
  color: $color-grey-darkest;
  transition: color 0.25s;
  transition: background-color 0.25s, background-image 0.25s;
}

////////////
// Layout //
////////////
.header {
  margin: 0 0 3rem 0;
  font-size: 0.85rem;
}

.page {
  padding: 2rem;
  background: $color-white;
  transition: background-color 0.25s, border-top-color 0.25s;
}

.contact-info {
  margin-top: 2rem;
}

.intro {
  font-size: 1.25rem;
  margin: 0 0 3rem 0;
}

.expertise {
  margin-bottom: 2rem;
}

.expertise-item {
  margin-bottom: 0.5rem;
}

.employment {
  margin-bottom: 2rem;
}

.employment-info {
  font-size: 0.9rem;
  margin-bottom: 0.75rem;
  font-style: italic;
}

//////////
// Text //
//////////
p {
  margin: 0 0 0.25rem 0;
}

ul {
  list-style: square;
  margin: 0;
  padding: 0 0 0 1rem;
}

address {
  margin: 0 0 0.25rem 0;
  font-style: italic;
}

h1 {
  margin: 0;
  font-weight: normal;
  font-style: italic;
}

h2 {
  font-size: 1.25rem;
  margin: 0;
  font-weight: normal;
  color: $color-pink;
  transition: color 0.25s;
}

h3 {
  font-size: 1.25rem;
  margin: 0 0 1rem;
  font-weight: bold;
}

h4 {
  font-size: 1rem;
  margin: 0 0 0.25rem 0;
  font-weight: bold;
}

///////////
// Links //
///////////
a {
  color: $color-blue;
  text-decoration: none;
  border-bottom: 1px solid $color-blue;
  transition: color 0.25s, border-bottom-color 0.25s;
}

a:link,
a:visited
a:active {
  color: $color-blue;
}

a:hover,
a:focus {
  color: $color-navy;
  border-bottom: 1px solid $color-navy;
}

@media (min-width: 801px) {
  .expertise {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
  }
  
  .expertise-item {
    flex: 1 0 48%;
    margin-left: 2%;
    padding-right: 1rem;
  }

  .employment {
    max-width: 80%;
  }
}

