    color: $color-aqua;
    border-bottom-color: $color-aqua;
  }
  
  a:link,
  a:visited {
    color: $color-aqua;
  }
  
  a:hover,
  a:focus {
    color: $color-blue;
    border-bottom-color: $color-blue;
  }
}
